<template>
  <div id="scroll">
    <transition name="fade">
      <div class="scrtoll-Btn" v-if="isShow" @click="scrollTop">
        <i class="fas fa-arrow-circle-up Page-Btn-Icon"></i>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isShow: false
    }
  },
  mounted () {
    // スクロール・イベントを追加
    window.addEventListener('scroll', () => {
      this.isShow = (window.scrollY > 100)
    })
  },
  methods: {
  // トップに戻る
    scrollTop: function () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>
<style>
.scrtoll-Btn{
  position: fixed;
  right: 28px;
  bottom: 28px;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
}
.Page-Btn-Icon{
  right: 15px;
  bottom: 15px;
  color: #999999;
  background-color: rgba(136, 132, 132, 0.5);
  border-radius: 28px;
  font-size: 50px;
}
.scroll-top {
  position: fixed;
  bottom: 32px;
  right: 32px;
  background-color: #000;
  padding: 10px 16px;
  border-radius: 32px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



