<template>
<div>
  <hooper :settings="hooperSettings" style="height: 550px">
  <slide>
    <span class="label">
        <img src="../img/image2.jpeg">
    </span>
  </slide>
  <slide>
    <span class="label">
      <img src="../img/image1.jpeg">
    </span>
  </slide>
  <slide>
    <span class="label">
        <img src="../img/image3.jpeg">
    </span>
  </slide>
  <slide>
    <span class="label">
      <img src="../img/image4.jpeg">
      </span>
  </slide>
  <slide>
    <span class="label">
      <img src="../img/image5.jpeg">
      </span>
</slide>
<hooper-pagination slot="hooper-addons"></hooper-pagination>
</hooper>
</div>
</template>
<script>
export default {
  components: {
    Hooper: window.Hooper.Hooper,
    Slide: window.Hooper.Slide,
    Pagination: window.Hooper.HooperPagination
  },
  data () {
    return {
      hooperSettings: {
        itemsToShow: 1,
        autoPlay: true,
        playSpeed: 3000,
        transition: 3000,
        hoverPause: false
      }
    }
  }
}
</script>
<style scoped>
.hooper-slide {
    background-color: #fff;
    padding: 0px;
    display: block;
    justify-content: center;
    align-items: center;
    color: #fff;
    border: none;
    font-size: 30px;
  }
img{
    width: 600px;
    height: 450px;
}
@media screen and (max-width: 700px) {
  img{
    width: 80%;
    height: 300px;
  }
}
</style>



