<template>
<div>
<div class = "L-tewelve">
  <div class = "row">
<div class = "banner-wrapper-fixed">
   <div class = "foreground">
     <h1>PARALLAX SCROLLING</h1>
   </div>
 </div>
 <div class = "wrapper">
 </div>
  </div>
</div>
<div class = "L-tewelve">
  <div class = "row">
    <div class = "section">
      <h1 class = "heading">TOPICS</h1>
    </div>
  </div>
</div>
<div class = "L-tewelve">
  <div class = "row">
    <div class = "section">
      <div class = "L-four T-six S-tewelve">
        <!-- カルーセル -->
        <Carousel />
      </div>
    </div>
  </div>
</div>
<div class = "L-tewelve">
  <div class = "row">
    <footer>
      <p>Made with <span class = "heart">&hearts;</span> from Amir Saleem</p>
    </footer>
  </div>
</div>
</div>
</template>
<script>
import Carousel from '../components/Carousel.vue'
import About from './about.vue'

export default {
  components: {
    Carousel,
    About
  },
  name: 'Top',
  props: ['message']
}
window.addEventListener('scroll', function () {
  parallax('banner-wrapper-fixed', 5, 0)
})
function parallax (elem, speed, number) {
  var target = document.getElementsByClassName(elem)[number]
  var scrollValue = window.pageYOffset
  if (scrollValue <= 700) {
    target.style.transform = 'translateY(' + (-scrollValue / speed) + 'px)'
  }
}
</script>
<style scoped>
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: sans-serif;
  color: #1e1e1e;
  background: white;
  height: 100%;
}
.L-tewelve {
  background-color: white;
}
img {
  display: block;
  max-width: 100%;
}
.wrapper {
  position: relative;
  height: 600px;
}
.banner-wrapper-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  background-image: url('../img/image2.jpeg');
  background-position: center;
  background-size: cover;
  transition: all 0.1s ease 0s;
}
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
}
.banner-wrapper-fixed .foreground h1 {
  text-align: center;
  color: white;
  margin-top: 250px;
  font-size: 50px;
  padding: 0px 30px;
}
.heading {
  padding: 20px 0px;
  font-size: 40px;
  margin: 0;
  text-align: center;
}
.section {
  position: relative;
  width: 100%;
  height: auto;
  background-color: white;
}
.box {
  position: relative;
  width: 100%;
  height: 300px;
  background-color: white;
  margin: 10px 0px 30px 0px;
  -webkit-box-shadow: 0px 0px 2px 2px #ccc;
  box-shadow: 0px 0px 2px 2px #ccc;
}
footer {
  position: relative;
  width: 100%;
  height: 50px;
  background-color: #003333;
}
footer p {
  text-align: center;
  margin: 0;
  color: white;
  padding: 10px 0px;
  font-size: 12px;
}
.heart {
  font-size: 18px;
  margin: 0 4px;
  color: crimson;
}
</style>



