<template>
<div id="nav">
<nav class="navbar" role="navigation" aria-label="main navigation">
  <div class="navbar-brand">
    <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample" @click="isOpen = !isOpen" v-bind:class="{'is-active': isOpen}">
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
    </a>
  </div>

  <div id="navbarBasicExample" class="navbar-menu" v-bind:class="{'is-active': isOpen}">
    <div class="navbar-end">
      <RouterLink class="navbar-item" to="/">TOP</RouterLink>

      <RouterLink class="navbar-item" to= "/about">ABOUT</RouterLink>

      <a class="navbar-item">XXXX</a>

      <a class="navbar-item">OTHER</a>
    </div>
  </div>
</nav>
</div>
</template>
<script>
export default {
  data () {
    return {
      isOpen: false,
      breakpoints: {

      }
    }
  },
  watch: {
    '$route': function (n, o) {
      if (n.hash.match(/^#/)) {
        document.getElementById(n.hash.replace(/^#/, '')).scrollIntoView()
      }
      console.log('new, old', [n.hash, o.hash])
    }
  },
  mounted () {
    if (this.$route.hash.match(/^#/)) {
      document.getElementById(this.$route.hash.replace(/^#/, '')).scrollIntoView()
    }
  }
}
</script>
<style scoped>
.navbar{
  background-color: black;
}
.navbar-item{
  color: black;
  text-align: center;
}
.navbar-item{
  color: white;
  text-align: center;
}
@media screen and (max-width: 1028px) {
  .navbar-item{
  color: black;
  text-align: center;
}
}
</style>



